
.line-heading {
  border-bottom: solid #999 1px;
  text-align: right;
}
.line-heading .line-label {
    text-transform: capitalize;
    font-weight: 100;
    text-align: left;
}
.line-heading .line-label > strong {
      padding-left: .25em;
}
.line-total {
  border-top: solid #CCC 1px;
}
.line-total > * {
    border-right: solid #CCC 1px;
}
.details {
  max-width: 100%;
}
.details td, .details th {
    padding: 2px !important;
}
.details thead th {
    text-align: center;
    font-weight: 100;
}
.details thead th small {
      display: block;
}
.details thead th strong {
      font-size: 1.25em;
}
.details tbody {
    text-align: right;
}
.details tbody tr:nth-child(even) {
      background: rgba(0, 0, 0, 0.05);
}
.duration {
  color: #0b58a2;
  text-align: right;
}
.nothing {
  color: #8f97a0;
}
.line {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.line > * {
    -webkit-box-flex: 1;
        -ms-flex: 1 0 0px;
            flex: 1 0 0;
    padding: 2px;
}
.line small {
    text-align: center;
    color: rgba(0, 0, 0, 0.25);
}
.line small.state {
    background: #5c646c;
    color: #FFFFFF;
    padding: 0 4px 0 2px;
    text-shadow: -1px 1px 0 rgba(0, 0, 0, 0.3);
    border-radius: 2px;
    font-weight: 400;
}
.line small.state.state-none {
      background: #8f97a0;
}
.line small.state.state-valid {
      background: #3a8104;
}
.line small.state.state-conflict {
      background: #9a1e1b;
}
.line small.state.state-send-prj, .line small.state.state-send-sci, .line small.state.state-send-adm {
      background: #0b93d5;
}
.line .line-label {
    -webkit-box-flex: 3;
        -ms-flex: 3 0 0px;
            flex: 3 0 0;
}
.line .empty-period {
    -webkit-box-flex: 10;
        -ms-flex: 10 1 0px;
            flex: 10 1 0;
    text-align: center;
}
.line .duration {
    text-align: right;
}
.line-datas {
  border-left: 8px #5c646c solid;
}
.line-datas:nth-child(even) {
    background: rgba(0, 0, 0, 0.05);
}
.line-datas:hover {
    background: rgba(11, 88, 162, 0.25);
}
.line-datas.send-prj {
    border-left-color: #5c9ccc;
}
.line-datas.send-sci {
    border-left-color: #5c9ccc;
}
.line-datas.send-adm {
    border-left-color: #5c9ccc;
}
.line-datas.valid {
    border-left-color: #3a8104;
}
.line-datas.conflic {
    border-left-color: #dd1144;
}
.line-datas > * {
    border-right: solid #CCC 1px;
}



